import { useEffect, useState } from 'react';
import Article from '../interop/Article';
import Contentful from './Contentful';

const useEditorialArticles = () => {
  const [articles, setArticles] = useState<Article[]>([]);
  const [ready, setReady] = useState<boolean>(false);

  const contenful = new Contentful();


  useEffect(() => {
    contenful.getArticles().then((response: any) => {
      const articles = response.items.map((content: any) => {
        const { image, video, title, author } = content.fields;
        const imageUrl = image?.fields.file.url;
        const videoUrl = video?.fields.file.url;

        return {
          title,
          author,
          link: `/editorial/${content.sys.id}`,
          date: content.sys.createdAt,
          image: imageUrl ? `https:${imageUrl}` : undefined,
          video: videoUrl ? `https:${videoUrl}` : undefined,
        }
      });
      setArticles(articles);
      setReady(true);
    });
  }, []);

  return {
    ready,
    articles
  };
};

export default useEditorialArticles;
