import { Grid, Typography } from "@mui/material";
import { Box } from "@mui/system";
import { Link } from "react-router-dom";
import Article from "../../interop/Article";
import './Item.css';

export interface ItemProps {
    article: Article;
};

export default function Item(props: ItemProps) {

    const art = props.article;
    const date = new Date(art.date);

    return (
        <Box sx={{ padding: '30px 0', borderBottom: '1px solid #0003' }}>
            <div className="listing-item">
                {art.video && <video controls src={art.video}/>}
                {!art.video && art.thumbnail && <img src={art.thumbnail}/>}
                <Typography variant="h6"><Link to={art.link}>{art.title}</Link></Typography>
                {art.originalLink && <p>Source <a href={art.originalLink} target="_blank" rel="noopener noreferrer">here</a></p>}
                <Typography variant="subtitle2">{date.toLocaleDateString('ru-RU')}, {date.toLocaleTimeString('ru-RU')}</Typography>
            </div>
        </Box>
    );
};
