import { Typography } from '@mui/material';
import Box from '@mui/material/Box';

const ArticleBlock = ({ block }: any) => {
  const formatted = block.bodyHtml.replace('https://www.theguardian.com', `${window.origin}/guardian`);

  return (
    <Box sx={{ padding: '30px 0', borderBottom: '1px solid #0003' }}>
      <Typography variant='h4'>{block.title}</Typography>
      <Typography variant='overline'>{block.publishedDate}</Typography>
      <Typography variant='body2'>
        <div dangerouslySetInnerHTML={{ __html: formatted }} />
      </Typography>
    </Box>
  );
};

export default ArticleBlock;

