import Contentful from './Contentful';
import { ReactNode, useEffect, useState } from 'react';
import Article from '../interop/Article';
import { documentToReactComponents } from '@contentful/rich-text-react-renderer';

interface EditorialArticle extends Article {
  content: ReactNode;
  image?: string;
  caption?: ReactNode;
}

const useArticle = (id: string): { article: EditorialArticle | undefined } => {
  const [article, setArticle] = useState<EditorialArticle | undefined>();
  const contenful = new Contentful();


  useEffect(() => {
    contenful.getArticle(id).then((response: any) => {
      const { article, image, video, caption } = response.items[0].fields;
      const imageUrl = image?.fields.file.url;
      const videoUrl = video?.fields.file.url;

      const content = article ? documentToReactComponents(article) : undefined;
      const cap = caption ? documentToReactComponents(caption) : undefined;
      const item = {
        title: response.items[0].fields.title,
        author: response.items[0].fields.author,
        link: `/editorial/${response.items[0].sys.id}`,
        date: response.items[0].sys.createdAt,
        content,
        image: imageUrl ? `https:${imageUrl}` : undefined,
        video: videoUrl ? `https:${videoUrl}` : undefined,
        caption: cap
      };
      setArticle(item);
    });
  }, [id]);

  return { article };
};

export default useArticle;
