import Guardian from './Guardian';
import { useEffect, useState } from 'react';

const useGuardianArticle = (id: string) => {
  const [content, setContent] = useState<any>();
  const guardian = new Guardian();


  useEffect(() => {
    guardian.getArticle(id).then((response: any) => {
      setContent(response.response.content);
    });
  }, [id]);

  return {
    body: content?.blocks.body || [],
    main: content?.blocks.main,
    title: content?.webTitle || '',
    date: content?.webPublicationDate
  };
};

export default useGuardianArticle;
