import config from '../config';
import { get } from '../Request';


interface GuardianFilter {
  q?: string; // query term
  section?: string;
  tag?: string;
  'from-date'?: string; // 2022-02-20
  'api-key': string;
  page?: string;
  'show-elements'?: 'all';
}

interface Filter extends Omit<GuardianFilter, 'from-date' | 'api-key'> {
  type?: 'liveblog' | 'article';
  fromDate?: string; // 2022-02-20
}


export default class Guardian {
  public static url: string = 'https://content.guardianapis.com/';
  public static apiKey: string = config.guardianApiKey;
  public static uaLiveBlogUrl: string = 'https://content.guardianapis.com/world/series/ukraine-live';

  async search(filters: Filter) {
    const fromDate = filters.fromDate;
    delete filters.fromDate;
    const guardianFilters: GuardianFilter = {
      ...filters,
      'from-date': fromDate,
      'api-key': Guardian.apiKey
    };

    const search = new URLSearchParams((guardianFilters as any));
    const url = new URL(`/search?${search}`, Guardian.url);
    return get(url);

  }

  async uaLiveBlog(page: number) {
    const url = new URL(`?page=${page}&api-key=${Guardian.apiKey}`, Guardian.uaLiveBlogUrl);
    return get(url);
  }

  async getArticle(id: string) {
    const url = new URL(`/${id}?show-blocks=body,main&show-elements=all&api-key=${Guardian.apiKey}`, Guardian.url);
    return get(url);
  }

}
