import './article.css';
import { useLocation } from 'react-router-dom';
import ArticleBlock from './ArticleBlock';
import useArticle from './useArticle';
import Typography from '@mui/material/Typography';
import { Container } from '@mui/material';
import BackButton from '../components/BackButton';


const Article = () => {
  const { pathname } = useLocation();
  const id = pathname.replace(/\/guardian-[a-z|A-Z]+\//, '');
  const { body, main, title, date } = useArticle(id);
  let formatted = main?.bodyHtml.replace('https://www.theguardian.com', `${window.origin}/guardian`);


  return (
    <Container maxWidth="md">
      <BackButton />
      <Typography variant="h3">{title}</Typography>
      <Typography variant="subtitle2">{date}</Typography>
      {formatted && <div dangerouslySetInnerHTML={{ __html: formatted }} />}
      {body.map((b: any) => <ArticleBlock key={b.id} block={b} />)}
    </Container>
  )
}

export default Article;
