import Guardian from './Guardian';
import { useEffect, useState } from 'react';
import Article from '../interop/Article';

const useArticles = (page: number = 1, type: string) => {
  const [articles, setArticles] = useState<Article[]>([]);
  const [currentPage, setCurrentPage] = useState<number>(0);
  const [pages, setPages] = useState<number>(0);
  const [pageSize, setPageSize] = useState<number>(0);
  const [ready, setReady] = useState<boolean>(false);
  const guardian = new Guardian();

  useEffect(() => {
    let promise: any;
    if (type === 'recent') {
      console.log('recent');
      promise = guardian.search({
        section: 'world',
        fromDate: '2022-02-20',
        q: 'ukraine',
        page: `${page}`
      });
    }

    if (type === 'liveblog') {
      console.log('liveblog');
      promise = guardian.uaLiveBlog(page);
    }

    promise.then((data: any) => {
      const { response } = data;
      const arts = response.results.map((item: any) => {

        return {
          title: item.webTitle,
          link: `/guardian-${type}/${item.id}`,
          date: item.webPublicationDate
        }
      });
      setArticles(arts);
      setCurrentPage(response.currentPage);
      setPages(response.pages);
      setPageSize(response.pageSize);
      setReady(true);
    });

  }, [page, type]);

  return {
    articles: articles || [],
    currentPage,
    pageSize,
    pages,
    ready
  };
};

export default useArticles;
