import './editorial.css';
import { useLocation } from 'react-router-dom';
import useArticle from './useArticle';
import Typography from '@mui/material/Typography';
import Loader from '../widgets/Loader';
import BackButton from '../components/BackButton';

const Article = () => {
  const { pathname } = useLocation();
  const articleId = pathname.replace('/editorial/', '');
  const { article } = useArticle(articleId);

  if (!article) {
    return <Loader />
  }

  const { title, date, content, author, video, image, caption } = article!;
  const d = new Date(date);

  return (
    <>
      <BackButton />
      <Typography variant="h4">{title}</Typography>
      <Typography variant="subtitle2">{d.toLocaleDateString('ru-RU')}, {d.toLocaleTimeString('ru-RU')}</Typography>
      {caption && caption}
      {image && <div><img className='editorial-image' src={image} alt={title} /></div>}
      {video &&
        <div>
          <video controls height="600">
            <source src={video} />
            Sorry, your browser doesn't support embedded videos.
          </video>
        </div>
      }
      {content && content}
      <Typography variant="overline">{author}</Typography>
    </>
  )
};

export default Article;
