export interface FetchConfig {
  method?: 'GET' | 'POST' | 'PUT' | 'DELETE';
  headers?: Record<string, string>,
  token?: string;
  body?: BodyInit
}

export const get = <T>(url: URL, config?: FetchConfig ): Promise<T> =>
  request(url, {
    method: 'GET',
    ...config
  }).then((response) => response.json());

export const post = async <T>(url: URL, config?: FetchConfig): Promise<T> =>
  request(url, {
    method: 'POST',
    ...config
  })
    .then((response) => response.json());

const request = async (url: URL, config?: FetchConfig) => {
  const headers = new Headers();

  if (config?.headers) {
    Object.keys(config.headers).forEach(key => headers.append(key, config.headers![key]));
  }

  return fetch(`${url}`, {
    body: config?.body,
    headers: createHeader(config?.token, config?.headers),
    method: config?.method || 'GET'
  }).then(handleResponse);
}

const createHeader = (token?: string, additionalHeaders?: Record<string, string>) => {
  const headers = new Headers();

  headers.append('Content-Type', 'application/json');
  headers.append('Accept', 'application/json');

  if (additionalHeaders) {
    Object.keys(additionalHeaders).forEach(key => headers.append(key, additionalHeaders![key]));
  }

  return headers;
};

const handleResponse = async (response: Response) => {
  if (response.status >= 200 && response.status <= 204) {
    return response;
  }
  throw response;
};
