import './navigation.css'
import { AppBar, Divider, Drawer, IconButton, List, ListItem, Toolbar, Typography } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { useState } from 'react';
import { Link } from 'react-router-dom';

const Navigation = () => {
  const [open, setOpen] = useState<boolean>(false);

  const toggleDrawer = (open: boolean) => (event: any) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setOpen(open);
  };

  const list = () => (
    <div
      className='navigation'
      role="presentation"
      onClick={toggleDrawer(false)}
      onKeyDown={toggleDrawer(false)}
    >
      <List>
        <li className='subheader'>
          От автора
        </li>
        <ListItem>
          <Link to="/editorial">Глазами украинцев</Link>
        </ListItem>
        <li className='subheader'>
          The Guardian
        </li>
        <ListItem>
          <Link to="/guardian-liveblog">Живой блог</Link>
        </ListItem>
        <ListItem>
          <Link to="/guardian-recent">Последние статьи</Link>
        </ListItem>
        <li className='subheader'>
          Reddit
        </li>
        <ListItem>
          <Link to="/reddit-worldnews">r/worldnews</Link>
        </ListItem>
        <ListItem>
          <Link to="/reddit-UkraineConflict">r/UkraineConflict</Link>
        </ListItem>
        <ListItem>
          <Link to="/reddit-wallstreetbets">r/wallstreetbets</Link>
        </ListItem>
      </List>
      <Divider />
    </div>
  );

  return (
    <>
      <AppBar position="static">
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={toggleDrawer(true)}
            edge="start"
            sx={{ mr: 2, ...(open && { display: 'none' }) }}
          >
            <MenuIcon /><Typography sx={{ marginLeft: '10px' }}>Все ресурсы</Typography>
          </IconButton>
        </Toolbar>
      </AppBar>
      <Drawer
        anchor='left'
        open={open}
        onClose={toggleDrawer(false)}
      >
        {list()}
      </Drawer>
    </>

  )
};

export default Navigation;
